$color-control-icon: #333; // Cor dos ícones de navegação

.body-imovel {
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100vh;

  .main-imovel {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100vh;

    .header-imovel {
      background-color: black;
      color: white; // Cor do texto no cabeçalho
      max-height: 15vh;
      padding: 20px;
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      gap: 40px;

      .left {
        a {
          i {
            color: var(--color-highlight);
            font-size: 2.4rem;
          }
        }
      }

      .center {
        display: flex;
        align-items: center;
        flex-direction: row;
        gap: 20px;
        justify-content: center;

        img {
          width: 120px;
          height: auto;
        }

        .header-text {
          p {
            font-weight: bold;
            color: white;
            font-size: 1.2rem;
          }
        }
      }

      .links-imovel {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        gap: 20px;

        a {
          i {
            color: var(--color-highlight);
            font-size: 1.8rem;
          }
        }
      }
    }

    .content {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      padding: 20px;
      height: 80vh;

      h1 {
        color: var(--color-highlight); // Cor do título
        font-size: 2.3rem;
        font-weight: bold;
        margin: 0 0 15px 0;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
      }

      #imovel {
        margin-top: -20px;
        overflow-y: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        padding-left: 150px;
        padding-right: 150px;
        width: 100%;
        gap: 50px;

        .photos {
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
          width: 50%;

          .carousel {
            width: 100%;
            height: auto;

            .carousel-inner {
              box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
              width: 100%;
              height: auto;
              border-radius: 20px;

              .carousel-item {
                img {
                  border: 1px solid #ddd;
                  border-radius: 10px;
                  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
                  border-radius: 20px;
                  width: 500px;
                  height: 350px;
                  object-fit: cover;
                  min-width: 500px;
                  max-width: 500px;
                  min-height: 350px;
                  max-height: 350px;
                }
              }
            }

            .carousel-control-prev-icon,
            .carousel-control-next-icon {
              background-color: $color-control-icon; // Cor dos ícones de controle
              border-radius: 50%; // Ícones circulares
            }
          }
        }

        .informacoes-imovel {
          padding: 50px;
          margin-top: 30px;
          border-radius: 10px;

          div {
            margin-bottom: 10px;

            strong {
              color: var(--primary-color, #333); // Usando uma variável CSS ou cor padrão
            }
          }
        }
      }
    }

    .header-card-imovel {
      display: flex;
      flex-direction: column;
      align-items: start;
      justify-content: center;

      .endereco-imovel {
        .title-card {
          font-weight: bold;
          font-size: 1.4rem;
          color: black;
        }
      }

      .codigoo-imovel {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: start;
        justify-content: space-between;
        //gap: 150px;

        .bairro-city {
          font-size: 1.4rem;
          color: #666;
        }

        .title-card {
          display: flex;
          flex-direction: row;
          font-weight: bold;
          font-size: 1.4rem;
          color: black;

          p {
            margin-right: 5px;
            font-size: 1.4rem;
          }
        }
      }
    }

    .infos-details {
      display: flex;
      flex-direction: row;
      gap: 30px;
      width: 100%;
      align-items: center;
      justify-content: center;
    }

    .description {
      text-align: justify;
      font-size: 1.2rem;
      color: #666;
      margin-top: 20px;
    }

    .whatsapp-button {
      margin-top: 20px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .footer-imoveis {
      width: 100%;
      height: 5vh;
      text-align: center;
      padding: 10px;
      background-color: black;
      display: flex;
      align-items: center;
      justify-content: center;

      p {
        margin-top: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
        color: white;

        strong {
          margin-left: 5px;
          font-size: 10px;
        }
      }
    }
  }


}


// Media Queries for Mobile Devices
@media (max-width: 768px) {


  .body-imovel {

    width: 100%;
    margin: 0;
    padding: 0;
    overflow: scroll;
    height: auto;
    .main-imovel {

      overflow: scroll;
      height: auto;

      overflow-y: scroll;
      min-height: 100vh;

      .header-imovel {
        flex-direction: row;
        justify-content: space-around;
        padding: 1rem;
        gap: 10px;

        .links-imovel {
          display: none;
        }

        .center {
          width: 90%;

          img {
            width: 110px;
            height: auto;
          }

          .header-text p {
            width: 100%;
            text-align: justify;
            // font-weight: initial;
            font-size: 0.8rem;
          }
        }

        /*   .header-text p {
          font-size: 1rem;
        } */
      }

      .content {
        height: auto;
        overflow-y: scroll;
        padding: 2rem;

        h1 {
          font-size: 2.8rem;
          min-width: 100%;
          text-align: center;
          color: var(--color-tertiary);
          margin: 1rem 0 2rem 0;
        }

        #imovel {
          height: auto;


          padding: 2rem;

          flex-direction: column;
          padding-left: 0;
          padding-right: 0;
          gap: 20px;
          margin-top: 30px;

          .photos {
            .carousel {

              .carousel-control-prev,
              .carousel-control-next {
                margin-top: 40px;
              }
            }
          }

          .photos {
            margin-top: -50px;
            height: 340px;
           
          }


          .photos,
          .informacoes-imovel {
            width: 100%;
          }

          .photos .carousel .carousel-inner .carousel-item img {
            width: 100%;
            min-width: 100%;
            max-width: 100%;
            height: 350px;
            margin-top: 30px;
            min-height: auto;
            max-height: auto;
            border-radius: 20px;




            .carousel-control-prev {
              margin-top: 200px;
            }
          }

          .informacoes-imovel {
            flex-direction: column;
            padding: 0;
            margin-top: 0;

            .header-card-imovel {
              padding: 0 2rem;

              .endereco-imovel {
                .title-card {
                  font-size: 1.4rem;
                }
              }

              .codigoo-imovel {
                .title-card {
                  font-size: 1.2rem;
                }
              }
            }

            .description {
              padding: 0 2rem;
              font-size: 1.2rem;

            }

            .whatsapp-button {
              margin: 40px 0 70px 0;
              width: 100%;
              display: flex;
              align-items: center;
              justify-content: center;




            }



          }

        }
      }
    }

    .footer-imoveis {
      padding: 5px;
      position: fixed;
      bottom: 0;

      p {
        font-size: 8px;
      }
    }
  }

}